import * as React from 'react'
import { HeadFC, PageProps, graphql, Link } from 'gatsby'

import Seo from '../components/Seo'
import Bio from '../components/Bio'
import Layout from '../components/Layout'

const IndexPage = ({ data, location }: PageProps) => {
  console.log(data, location)
  return (
    <Layout>
      <Bio />
      <Link to="/blog">블로그로 이동</Link>
    </Layout>
  )
}

export default IndexPage

/**
 * Head export to define metadata for the page
 *
 * See: https://www.gatsbyjs.com/docs/reference/built-in-components/gatsby-head/
 */
export const Head: HeadFC = () => <Seo title="blog" />

/**
 * Querying Data in Pages with GraphQL
 *
 * https://www.gatsbyjs.com/docs/how-to/querying-data/page-query/
 * https://www.gatsbyjs.com/docs/tutorial/getting-started/part-4/
 */
export const pageQuery = graphql`
  {
    site {
      siteMetadata {
        title
      }
    }
  }
`
